<template>
  <main class="content container">
    <div class="product-loading" v-if="productLoading">
      <p>Загрузка каталога...</p>
      <img src="img/4.gif" alt="loading" />
    </div>
    <div class="product-loading-failed" v-if="productLoadingFailed">
      <p>Ошибка загрузки...</p>
      <button class="failed-btn" @click.prevent="loadProducts">
        Попробовать еще раз
      </button>
    </div>
    <div v-if="dataLoaded">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              {{ product.category.title }}
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link"> {{ product.title }} </a>
          </li>
        </ul>
      </div>

      <section class="item">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img
              width="570"
              height="570"
              :src="curentImg"
              alt="Название товара"
            />
          </div>
          <ul class="pics__list">
            <li
              class="pics__item"
              v-for="(img, index) in product.colors"
              :key="img.id"
            >
              <a href="" class="pics__link">
                <img
                  width="98"
                  height="147"
                  :src="img.gallery[0].file.url"
                  :alt="img.gallery[0].file.originalName"
                  @click.prevent="showImg(index)"
                />
              </a>
            </li>
          </ul>
        </div>

        <div class="item__info">
          <span class="item__code">Артикул: {{ product.id }}</span>
          <h2 class="item__title">{{ product.title }}</h2>
          <div class="item__form">
            <form
              class="form"
              action="#"
              method="POST"
              @submit.prevent="addToCart"
            >
              <div class="item__row item__row--center">
                <div class="form__counter">
                  <button
                    type="button"
                    aria-label="Убрать один товар"
                    @click="minusAmount"
                  >
                    <svg width="12" height="12" fill="currentColor">
                      <use xlink:href="#icon-minus"></use>
                    </svg>
                  </button>

                  <input
                    type="text"
                    name="count"
                    v-model.number="productAmount"
                  />

                  <button
                    type="button"
                    aria-label="Добавить один товар"
                    @click="productAmount++"
                  >
                    <svg width="12" height="12" fill="currentColor">
                      <use xlink:href="#icon-plus"></use>
                    </svg>
                  </button>
                </div>

                <b class="item__price">
                  {{ product.price | numberformat }} ₽
                </b>
              </div>

              <div class="item__row">
                <fieldset class="form__block">
                  <legend class="form__legend">Цвет</legend>
                  <ul class="colors colors--black">
                    <li
                      class="colors__item"
                      v-for="(color, index) in product.colors"
                      :key="color.color.id"
                      @click="showImg(index)"
                    >
                      <label class="colors__label">
                        <input
                          class="colors__radio sr-only"
                          type="radio"
                          name="color-item"
                          :value="color.color.id"
                          v-model.number="productColorId"
                        />
                        <span
                          class="colors__value"
                          :style="`background-color: ${color.color.code}`"
                        >
                        </span>
                      </label>
                    </li>
                  </ul>
                </fieldset>

                <fieldset class="form__block">
                  <legend class="form__legend">Размер</legend>
                  <label
                    class="form__label form__label--small form__label--select"
                  >
                    <select
                      class="form__select"
                      type="text"
                      name="category"
                      v-model.number="productSizeId"
                    >
                      <option
                        v-for="size in product.sizes"
                        :key="size.id"
                        :value="size.id"
                      >
                        {{ size.title }}
                      </option>
                    </select>
                  </label>
                </fieldset>
              </div>

              <button class="item__button button button--primery" type="submit">
                В корзину
              </button>
              <div v-show="wrongSizeAndColor">Укажите цвет и размер</div>
              <div v-show="productAdded">Товар добавлен в корзину</div>
              <div v-show="productAddSending">Добавляем товар в корзину</div>
            </form>
          </div>
        </div>

        <div class="item__desc">
          <ul class="tabs">
            <li class="tabs__item">
              <a
                class="tabs__link tabs__link--current"
                @click.prevent="showProductInfo"
              >
                Информация о товаре
              </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#" @click.prevent="showDeliveryInfo">
                Доставка и возврат
              </a>
            </li>
          </ul>

          <div class="item__content" v-if="productInfo">
            <h3>Состав:</h3>

            <p v-for="material in product.materials" :key="material.id">
              {{ material.title }}
            </p>
            <p>
              {{ product.content }}
            </p>
          </div>
          <div class="item__content" v-if="deliveryInfo">
            <h3>Доставка:</h3>

            <p>
              1. Курьерская доставка по Москве и Московской области – 290 ₽<br />
              2.Самовывоз из магазина. Список и адреса магазинов Вы можете
              посмотреть здесь<br />
            </p>

            <h3>Возврат:</h3>

            <p>
              Любой возврат должен быть осуществлен в течение Возвраты в
              магазине БЕСПЛАТНО.<br />
              Вы можете вернуть товары в любой магазин. Магазин должен быть
              расположен в стране, в которой Вы осуществили покупку. БЕСПЛАТНЫЙ
              возврат в Пункт выдачи заказов.<br />
              Для того чтобы вернуть товар в одном из наших Пунктов выдачи
              заказов, позвоните по телефону 8 800 600 90 09<br />
            </p>
          </div>
        </div>
      </section>
    </div>
  </main>
</template>
<script>
import axios from "axios";
import { API_BASE_URL } from "@/config";
import numberformat from "@/helpers/number-format";
import { mapActions } from "vuex";
export default {
  name: "ProductPage",
  data() {
    return {
      productData: null,
      curentImg: ``,
      productAmount: 1,
      productColorId: null,
      productSizeId: null,
      checkedBox: false,

      productAdded: false,
      productAddSending: false,
      wrongSizeAndColor: false,

      productInfo: true,
      deliveryInfo: false,

      productLoading: false,
      productLoadingFailed: false,
      dataLoaded: false,
    };
  },
  filters: {
    numberformat,
  },
  computed: {
    product() {
      return this.productData ? this.productData : {};
    },
  },
  methods: {
    ...mapActions(["addProductToCart"]),
    addToCart() {
      if (this.productSizeId != null && this.productColorId != null) {
        this.productAdded = false;
        this.productAddSending = true;
        this.addProductToCart({
          productId: this.product.id,
          quantity: this.productAmount,
          sizeId: this.productSizeId,
          colorId: this.productColorId,
        }).then(() => {
          this.productAmount = 1;
          this.productColorId = null;
          this.productSizeId = null;
          this.productAdded = true;
          this.productAddSending = false;
          this.wrongSizeAndColor = false;
        });
      } else {
        this.wrongSizeAndColor = true;
        this.productAdded = false;
      }
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      axios
        .get(API_BASE_URL + "/api/products/" + this.$route.params.id)
        .then((response) => {
          this.productData = response.data;
          if (this.product.colors[0].gallery != null) {
            this.curentImg = this.product.colors[0].gallery[0].file.url;
          }
          if (this.product.colors[0].gallery == null) {
            this.curentImg = this.product.colors[1].gallery[0].file.url;
          }
          this.productLoading = false;
          this.dataLoaded = true;
        })
        .catch(() => (this.productLoadingFailed = true));
    },
    showImg(index) {
      const imgArr = document.querySelectorAll(".pics__link");
      imgArr.forEach((el) => el.classList.remove("active"));
      imgArr[index].classList.add("active");

      this.curentImg = this.product.colors[index].gallery[0].file.url;
    },
    minusAmount() {
      if (this.productAmount > 1) this.productAmount = this.productAmount - 1;
    },
    showProductInfo() {
      this.productInfo = true;
      this.deliveryInfo = false;
      document
        .querySelectorAll(".tabs__link")[0]
        .classList.add("tabs__link--current");
      document
        .querySelectorAll(".tabs__link")[1]
        .classList.remove("tabs__link--current");
    },
    showDeliveryInfo() {
      this.productInfo = false;
      this.deliveryInfo = true;
      document
        .querySelectorAll(".tabs__link")[0]
        .classList.remove("tabs__link--current");
      document
        .querySelectorAll(".tabs__link")[1]
        .classList.add("tabs__link--current");
    },
  },
  watch: {
    "$route.params.id": {
      handler() {
        this.loadProduct();
      },
      immediate: true, //позволяет избавиться от хука криэйтед
    },
  },
  // mounted() {
  //   this.loadProduct();
  // },
};
</script>
<style>
.pics__link img {
  height: 147px;
}

.active {
  border-color: #e02d71;
}
</style>
