<template>
  <li class="catalog__item">
    <router-link
      class="catalog__pic"
      :to="{ name: 'product', params: { id: product.id } }"
    >
      <img :src="curentImg" :alt="product.title" />
    </router-link>

    <h3 class="catalog__title">
      <a href="#"> {{ product.title }} </a>
    </h3>

    <span class="catalog__price">{{ product.price | numberformat }} ₽ </span>

    <ul class="colors colors--black">
      <li
        class="colors__item"
        v-for="(color, index) in product.colors"
        :key="color.color.id"
      >
        <label class="colors__label">
          <input
            class="colors__radio sr-only"
            type="radio"
            name="color-1"
            :value="color.color.id"
            checked=""
            v-model.number="curentColorId"
            v-on:change="getImgUrl(index)"
          />
          <span
            class="colors__value"
            :style="`background-color: ${color.color.code}`"
          >
          </span>
        </label>
      </li>
    </ul>
  </li>
</template>
<script>
import numberformat from "@/helpers/number-format";
export default {
  data() {
    return {
      curentColorId: 0,
      curentImg: ``,
    };
  },
  props: ["product"],
  filters: {
    numberformat,
  },
  methods: {
    getImgUrl(index) {
      this.curentImg = this.product.colors[index].gallery[0].file.url;
    },
    imgUrlOnCreate() {
      if (this.product.colors[0].gallery != null) {
        this.curentImg = this.product.colors[0].gallery[0].file.url;
      }
      if (this.product.colors[0].gallery == null) {
        this.curentImg = this.product.colors[1].gallery[0].file.url;
      }
    },
  },
  created() {
    this.imgUrlOnCreate();
  },
};
</script>
