import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import { API_BASE_URL } from "@/config";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
  },
  mutations: {
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    updateCartProductAmount(state, { basketItemId, quantity }) {
      const item = state.cartProducts.find(
        (item) => item.basketItemId === basketItemId
      );

      if (item) {
        item.quantity = quantity;
      }
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => {
        return {
          basketItemId: item.id,
          productTitle: item.product.title,
          productId: item.product.id,
          price: item.price,
          quantity: item.quantity,
          colorId: item.color.color.id,
          colorCode: item.color.color.code,
          colorCodeTilte: item.color.color.title,
          sizeId: item.size.id,
          img: item.color.gallery[0].file.url,
        };
      });
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts;
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => item.price * item.quantity + acc,
        0
      );
    },
  },
  actions: {
    loadCart(context) {
      // context.commit("cartLoading", loadCart);
      return axios
        .get(API_BASE_URL + "/api/baskets", {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem("userAccessKey", response.data.user.accessKey);
            context.commit("updateUserAccessKey", response.data.user.accessKey);
          }
          context.commit("updateCartProductsData", response.data.items);
          context.commit("syncCartProducts");
        });
    },
    addProductToCart(context, { productId, quantity, colorId, sizeId }) {
      return axios
        .post(
          API_BASE_URL + "/api/baskets/products",
          {
            productId: productId,
            quantity: quantity,
            sizeId: sizeId,
            colorId: colorId,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          context.commit("updateCartProductsData", response.data.items);
          context.commit("syncCartProducts");
        });
    },
    updateCartProductAmount(context, { basketItemId, quantity }) {
      context.commit("updateCartProductAmount", { basketItemId, quantity });

      if (quantity < 1) return;

      return axios
        .put(
          API_BASE_URL + "/api/baskets/products",
          {
            basketItemId: basketItemId,
            quantity: quantity,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          context.commit("updateCartProductsData", response.data.items);
          context.commit("syncCartProducts");
        })
        .catch(() => {
          context.commit("syncCartProducts");
        });
    },
    deleteCartProduct(context, basketItemId) {
      return axios
        .delete(API_BASE_URL + "/api/baskets/products", {
          data: { basketItemId },
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit("updateCartProductsData", response.data.items);
          context.commit("syncCartProducts");
        });
    },
    loadOrderInfo(context, orderId) {
      return axios
        .get(API_BASE_URL + "/api/orders/" + orderId, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit("updateOrderInfo", response.data);
        });
    },
  },
});
