<template>
  <aside class="filter">
    <form
      class="filter__form form"
      action="#"
      method="get"
      @submit.prevent="submit()"
    >
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="min-price"
            v-model.number="curentPriceFrom"
          />
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="max-price"
            v-model.number="curentPriceTo"
          />
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select
            class="form__select"
            type="text"
            name="category"
            v-model.number="curentCategoryId"
          >
            <option value="0">Все категории</option>
            <option
              :value="category.id"
              v-for="category in categories"
              :key="category.id"
            >
              {{ category.title }}
            </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors colors--black">
          <li class="colors__item" v-for="color in colors" :key="color.id">
            <label class="colors__label">
              <input
                class="colors__radio sr-only check-list__check"
                type="checkbox"
                name="color"
                :value="color.id"
                v-model="checked"
                @change.prevent="onChangeColorCheckbox(color.id)"
              />
              <span
                class="colors__value"
                :style="`background-color: ${color.code}`"
              >
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Материал</legend>
        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="material in materials"
            :key="material.id"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="material"
                v-model="checked"
                :value="material.id"
                @change.prevent="onChangeMaterialCheckbox(material.id)"
              />
              <span class="check-list__desc">
                {{ material.title }}
                <span>({{ material.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Коллекция</legend>
        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="season in seasons"
            :key="season.id"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="collection"
                v-model="checked"
                :value="season.id"
                @change.prevent="onChangeSeasonCheckbox(season.id)"
              />
              <span class="check-list__desc">
                {{ season.title }}
                <span>({{ season.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button
        class="filter__reset button button--second"
        type="button"
        @click="reset()"
        :disabled="isResetBtnDisabled"
      >
        Сбросить
      </button>
    </form>
  </aside>
</template>
<script>
import axios from "axios";
import { API_BASE_URL } from "@/config";
export default {
  model: {
    prop: "checked",
    event: "change",
  },
  data() {
    return {
      curentPriceFrom: 0,
      curentPriceTo: 0,
      curentCategoryId: 0,
      curentColorId: [],
      curentMaterialsId: [],
      curentSeasonId: [],

      checkedBox: false,

      categoriesData: null,
      colorsData: null,
      materialsData: null,
      seasonData: null,

      isResetBtnDisabled: true,
    };
  },
  props: [
    "priceFrom",
    "priceTo",
    "categoryId",
    "colorId",
    "materialsId",
    "seasonId",
  ],
  watch: {
    priceFrom(value) {
      this.curentPriceFrom = value;
    },
    priceTo(value) {
      this.curentPriceTo = value;
    },
    categoryId(value) {
      this.curentCategoryId = value;
    },
    //снимаем дизэйбл на кнопке ресет
    curentPriceFrom(value) {
      if (value > 0) {
        this.isResetBtnDisabled = false;
      }
      if (
        value == 0 &&
        this.curentPriceTo == 0 &&
        this.curentCategoryId == 0 &&
        this.curentColorId.length == 0 &&
        this.curentMaterialsId.length == 0 &&
        this.curentSeasonId.length == 0
      ) {
        this.isResetBtnDisabled = true;
      }
    },
    curentPriceTo(value) {
      if (value > 0) {
        this.isResetBtnDisabled = false;
      }
      if (
        value == 0 &&
        this.curentPriceFrom == 0 &&
        this.curentCategoryId == 0 &&
        this.curentColorId.length == 0 &&
        this.curentMaterialsId.length == 0 &&
        this.curentSeasonId.length == 0
      ) {
        this.isResetBtnDisabled = true;
      }
    },
    curentCategoryId(value) {
      if (value > 0) {
        this.isResetBtnDisabled = false;
      }
      if (
        value == 0 &&
        this.curentPriceTo == 0 &&
        this.curentPriceFrom == 0 &&
        this.curentColorId.length == 0 &&
        this.curentMaterialsId.length == 0 &&
        this.curentSeasonId.length == 0
      ) {
        this.isResetBtnDisabled = true;
      }
    },
    curentColorId() {
      if (this.curentColorId.length > 0) {
        this.isResetBtnDisabled = false;
      }
      if (
        this.curentColorId.length == 0 &&
        this.curentPriceTo == 0 &&
        this.curentPriceFrom == 0 &&
        this.curentCategoryId == 0 &&
        this.curentMaterialsId.length == 0 &&
        this.curentSeasonId.length == 0
      ) {
        this.isResetBtnDisabled = true;
      }
    },
    curentMaterialsId() {
      if (this.curentMaterialsId.length > 0) {
        this.isResetBtnDisabled = false;
      }
      if (
        this.curentMaterialsId.length == 0 &&
        this.curentPriceTo == 0 &&
        this.curentPriceFrom == 0 &&
        this.curentCategoryId == 0 &&
        this.curentColorId.length == 0 &&
        this.curentSeasonId.length == 0
      ) {
        this.isResetBtnDisabled = true;
      }
    },
    curentSeasonId() {
      if (this.curentSeasonId.length > 0) {
        this.isResetBtnDisabled = false;
      }
      if (
        this.curentSeasonId.length == 0 &&
        this.curentPriceTo == 0 &&
        this.curentPriceFrom == 0 &&
        this.curentCategoryId == 0 &&
        this.curentColorId.length == 0 &&
        this.curentMaterialsId.length == 0
      ) {
        this.isResetBtnDisabled = true;
      }
    },
  },
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
    materials() {
      return this.materialsData ? this.materialsData.items : [];
    },
    seasons() {
      return this.seasonData ? this.seasonData.items : [];
    },
    checked: {
      get() {
        return this.value;
      },
      set(val) {
        this.checkedBox = val;
      },
    },
  },
  methods: {
    submit() {
      this.$emit("update:priceFrom", this.curentPriceFrom);
      this.$emit("update:priceTo", this.curentPriceTo);
      this.$emit("update:categoryId", this.curentCategoryId);
      this.$emit("update:colorId", this.curentColorId);
      this.$emit("update:materialsId", this.curentMaterialsId);
      this.$emit("update:seasonId", this.curentSeasonId);
    },
    reset() {
      this.$emit("update:priceFrom", 0);
      this.$emit("update:priceTo", 0);
      this.$emit("update:categoryId", 0);
      this.$emit("update:colorId", []);
      this.$emit("update:materialsId", []);
      this.$emit("update:seasonId", []);
      document
        .querySelectorAll(".check-list__check")
        .forEach((element) => (element.checked = false));
      this.curentColorId = [];
      this.curentMaterialsId = [];
      this.curentSeasonId = [];
      this.checkedBox = false;
    },
    loadCategories() {
      axios
        .get(API_BASE_URL + `/api/productCategories`)
        .then((response) => (this.categoriesData = response.data));
    },
    loadColorsData() {
      axios.get(API_BASE_URL + `/api/colors`).then((response) => {
        this.colorsData = response.data;
      });
    },
    loadMaterialsData() {
      axios.get(API_BASE_URL + `/api/materials`).then((response) => {
        this.materialsData = response.data;
      });
    },
    loadSeasonData() {
      axios.get(API_BASE_URL + `/api/seasons`).then((response) => {
        this.seasonData = response.data;
      });
    },
    onChangeMaterialCheckbox(id) {
      this.$emit("input", this.checked);
      if (this.checkedBox === true) {
        this.curentMaterialsId.push(id);
        this.curentMaterialsId = this.curentMaterialsId.filter(function (
          element,
          index,
          array
        ) {
          if (array.lastIndexOf(element) == index) return element;
        });
      }
      if (this.checkedBox === false) {
        this.curentMaterialsId = this.curentMaterialsId.filter(
          (element) => element != id
        );
      }
    },
    onChangeSeasonCheckbox(id) {
      this.$emit("input", this.checked);
      if (this.checkedBox === true) {
        this.curentSeasonId.push(id);
        this.curentSeasonId = this.curentSeasonId.filter(function (
          element,
          index,
          array
        ) {
          if (array.lastIndexOf(element) == index) return element;
        });
      }
      if (this.checkedBox === false) {
        this.curentSeasonId = this.curentSeasonId.filter(
          (element) => element != id
        );
      }
    },
    onChangeColorCheckbox(id) {
      this.$emit("input", this.checked);
      if (this.checkedBox === true) {
        this.curentColorId.push(id);
        this.curentColorId = this.curentColorId.filter(function (
          element,
          index,
          array
        ) {
          if (array.lastIndexOf(element) == index) return element;
        });
      }
      if (this.checkedBox === false) {
        this.curentColorId = this.curentColorId.filter(
          (element) => element != id
        );
      }
    },
  },
  created() {
    this.loadCategories();
    this.loadColorsData();
    this.loadMaterialsData();
    this.loadSeasonData();
  },
};
</script>

<style>
.colors__value {
  border: 1px solid black;
}
</style>
