<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="product.img" width="120" height="120" alt="Название товара" />
    </div>
    <h3 class="product__title">{{ product.productTitle }}</h3>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="`background-color: ${product.colorCode}`"></i>
        {{ product.colorCodeTilte }}
      </span>
    </p>
    <span class="product__code"> Артикул: {{ product.productId }} </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click="amount--">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count" />

      <button type="button" aria-label="Добавить один товар" @click="amount++">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (product.price * product.quantity) | numberformat }} ₽
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(product.basketItemId)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>
<script>
import numberformat from "@/helpers/number-format";
import { mapActions } from "vuex";
export default {
  props: ["product"],
  filters: {
    numberformat,
  },
  computed: {
    amount: {
      get() {
        return this.product.quantity;
      },
      set(value) {
        if (value > 0) {
          this.$store.dispatch("updateCartProductAmount", {
            basketItemId: this.product.basketItemId,
            quantity: value,
          });
        }
      },
    },
  },
  methods: {
    ...mapActions(["deleteCartProduct"]),
    deleteProduct(basketItemId) {
      this.deleteCartProduct(basketItemId);
    },
  },
};
</script>
