<template>
  <main class="content container">
    <div class="content__top">
      <div class="content__row">
        <h1 class="content__title">Каталог</h1>
        <span class="content__info"> {{ products.length }} товара </span>
      </div>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :priceFrom.sync="filterPriceFrom"
        :priceTo.sync="filterPriceTo"
        :categoryId.sync="filterCategoryId"
        :colorId.sync="filterColorId"
        :materialsId.sync="filterMaterialId"
        :seasonId.sync="filterSeasonId"
      />
      <section class="catalog">
        <div class="product-loading" v-if="productsLoading">
          <p>Загрузка каталога...</p>
          <img src="img/4.gif" alt="loading" />
        </div>
        <div class="product-loading-failed" v-if="productsLoadingFailed">
          <p>Ошибка загрузки...</p>
          <button class="failed-btn" @click.prevent="loadProducts">
            Попробовать еще раз
          </button>
        </div>
        <ProductList :products="products" />
        <BasePagination
          v-model="page"
          :count="countProducts"
          :per-page="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>
<script>
import axios from "axios";
import { API_BASE_URL } from "@/config.js";
import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
export default {
  name: "App",
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: [],
      filterMaterialId: [],
      filterSeasonId: [],

      productsData: null,
      page: 1,
      productsPerPage: 12,
      count: 22,

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items : {};
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      axios
        .get(API_BASE_URL + `/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            colorIds: this.filterColorId,
            materialIds: this.filterMaterialId,
            seasonIds: this.filterSeasonId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
          },
        })
        .then((res) => {
          this.productsData = res.data;
          this.productsLoading = false;
        })
        .catch(() => (this.productsLoadingFailed = true));
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
    filterMaterialId() {
      this.loadProducts();
    },
    filterSeasonId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
<style>
.product-loading {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}
.product-loading img {
  margin-left: 20px;
}
.product-loading-failed {
  display: flex;
  align-items: center;
}
.failed-btn {
  padding: 10px 15px;
  margin-left: 30px;
  color: white;
  background-color: #e02d71;
  outline: none;
  border: none;
}
</style>
