<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">Оформление заказа</h1>
      </div>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="order"
      >
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              v-model="formData.name"
              :error="formError.name"
              type="text"
              title="ФИО"
              placeholder="Введите ваше полное имя"
            />

            <BaseFormText
              v-model="formData.address"
              :error="formError.address"
              type="text"
              title="Адрес доставки"
              placeholder="Введите ваш адрес"
            />

            <BaseFormText
              v-model="formData.phone"
              :error="formError.phone"
              type="text"
              title="Телефон"
              placeholder="Введите ваш телефон"
            />

            <BaseFormText
              v-model="formData.email"
              :error="formError.email"
              type="text"
              title="Email"
              placeholder="Введи ваш Email"
            />

            <BaseFormTextarea
              title="Комментарий к заказу"
              :error="formError.comment"
              v-model="formData.comment"
              placeholder="Ваши пожелания"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">
              Доставка
              <span class="error-order" v-if="formError.deliveryTypeId">{{
                formError.deliveryTypeId
              }}</span>
            </h3>

            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="1"
                    @change="onChangeRadioBtn('delivery', '1')"
                  />
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="2"
                    @change="onChangeRadioBtn('delivery', '2')"
                  />
                  <span class="options__value"> Курьером <b>1200 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">
              Оплата
              <span class="error-order" v-if="formError.paymentTypeId">{{
                formError.paymentTypeId
              }}</span>
            </h3>
            <span v-if="formError.paymentTypeId">{{
              formError.paymentTypeId
            }}</span>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="1"
                    @change="onChangeRadioBtn('pay', '1')"
                  />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="2"
                    @change="onChangeRadioBtn('pay', '2')"
                  />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="product in products"
              :key="product.productId"
            >
              <h3>{{ product.productTitle }}</h3>
              <b>{{ product.price | numberformat }} ₽</b>
              <span>Артикул: {{ product.productId }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>
              Доставка: <b>{{ deliveryType }}</b>
            </p>
            <p>
              Итого: <b>{{ products.length }}</b> товара на сумму
              <b
                >{{
                  totalPriceWithDelivery == null
                    ? totalPrice
                    : totalPriceWithDelivery | numberformat
                }}
                ₽</b
              >
            </p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
        <div v-if="formPreloader">Отправка данных..</div>
      </form>
    </section>
  </main>
</template>
<script>
import BaseFormText from "@/components/BaseFormText.vue";
import BaseFormTextarea from "@/components/BaseFormTextarea.vue";
import { API_BASE_URL } from "@/config";
import axios from "axios";
import { mapGetters } from "vuex";
import numberformat from "@/helpers/number-format";
export default {
  name: "OrderPage",
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: "",
      deliveryType: "",
      totalPriceWithDelivery: null,
      formPreloader: false,
    };
  },
  components: { BaseFormText, BaseFormTextarea },
  filters: {
    numberformat,
  },
  methods: {
    onChangeRadioBtn(name, type) {
      if (name === "delivery" && type === "1") {
        this.formData.deliveryTypeId = 1;
        this.deliveryType = "Бесплатно";
        this.totalPriceWithDelivery = this.totalPrice;
      }
      if (name === "delivery" && type === "2") {
        this.formData.deliveryTypeId = 2;
        this.deliveryType = "1200 ₽";
        this.totalPriceWithDelivery = this.totalPrice + 1200;
      }
      if (name === "pay" && type === "1") this.formData.paymentTypeId = 1;
      if (name === "pay" && type === "2") this.formData.paymentTypeId = 2;
    },
    order() {
      this.formError = {};
      this.formErrorMessage = "";
      this.formPreloader = true;
      setTimeout(() => {
        axios
          .post(
            API_BASE_URL + "/api/orders",
            {
              ...this.formData,
            },
            {
              params: {
                userAccessKey: this.$store.state.userAccessKey,
              },
            }
          )
          .then((response) => {
            this.$store.commit("resetCart");
            this.$store.commit("updateOrderInfo", response.data);
            this.$router.push({
              name: "orderInfo",
              params: { id: response.data.id },
            });
            this.formPreloader = false;
          })
          .catch((error) => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
            this.formPreloader = false;
          });
      }, 500);
    },
  },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
    }),
  },
};
</script>
<style>
.error-order {
  margin-left: 20px;
  font-size: 11px;
  line-height: 14px;
  color: #e02d71;
}
</style>
